.signup {
	max-width: 380px;
	background: #fff;
	border: 1px solid rgba(0, 0, 0, 0.12);
	box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
	border-radius: 4px;
	padding: 30px;
	padding-top: 40px;
	&__title {
		font-weight: 700;
		font-size: 24px;
		color: $dark;
		margin-bottom: 22px;
	}
	&__input {
		width: 100%;
		height: 44px;
		color: $dark75;
		border: 1px solid $dark25;
		border-radius: 4px;
		padding-left: 15px;
		margin-bottom: 10px;
		&::placeholder {
			color: $dark25;
		}
		&:focus {
			border: 1px solid $dark50;
		}
	}
	&__text {
		font-size: 12px;
		font-weight: 700;
		text-transform: uppercase;
		color: $dark;
		margin-bottom: 6px;
	}
	&__gender {
		margin-top: 2px;
		margin-left: -4px;
		margin-bottom: 27px;
	}
	&__birthdate {
		width: 100%;
		color: $dark75;
		margin-bottom: 21px;
	}
	&__toggle {
		margin-top: 2px;
		margin-left: -3px;
		margin-bottom: 25px;
	}
	&__btn {
		margin-bottom: 30px;
	}
	&__bottom {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	&__bottom-text {
		color: #000000;
	}
	&__bottom-btn {
		margin-right: -2px;
	}
}
@media (max-width: 445px) {
	.signup {
		padding: 15px;
	}
}
@media (max-width: 340px) {
	.toggle-elements__label {
		font-size: 12px;
	}
}
