.signin {
	max-width: 380px;
	padding: 30px;
	padding-top: 40px;
	background: #ffffff;
	border: 1px solid rgba(0, 0, 0, 0.12);
	box-sizing: border-box;
	box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
	border-radius: 4px;
	&__title {
		font-size: 24px;
		font-weight: 700;
		margin-bottom: 22px;
	}
	&__btn {
		margin: 10px 0 30px;
	}
	&__bottom {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	&__bottom-text {
		color: #000;
		vertical-align: top;
	}
	&__bottom-btn {
		margin-right: -2px;
	}
}
@media (max-width: 426px) {
	.signin {
		padding: 15px;
	}
}
