.header {
	height: 70px;
	background-color: #fff;
	box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
	&__container {
		height: 70px;
		display: flex;
		align-items: center;
	}
	&-btns {
		height: 33px;
		display: flex;
		align-items: center;
		margin-left: 20px;
		&--border {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 87px;
			height: 34px;
			text-align: center;
			border: double 2px transparent;
			border-radius: 25px;
			background-image: linear-gradient(180deg, $white, $white),
				radial-gradient(circle at top left, #bc9cff, #8ba4f9);
			background-origin: border-box;
			background-clip: content-box, border-box;
		}
		&__signin {
			font-family: "Montserrat";
			height: 34px;
			color: $purple;
			font-weight: 700;
			font-size: 12px;
			text-transform: uppercase;
			border-radius: 25px;
			padding: 0 20px;
			transition: box-shadow 0.3s ease-in, color 0.3s ease-in,
				opacity 0.3s ease-in;
			&:hover {
				color: darken($purple, 25%);
				opacity: 0.5;
				box-shadow: 0px 0px 10px 0px $purple;
			}
		}
		&__signup {
			font-family: "Montserrat";
			height: 34px;
			background: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
			color: $white;
			font-weight: 700;
			font-size: 12px;
			text-transform: uppercase;
			border-radius: 25px;
			padding: 0 19px;
			margin-left: 20px;
			transition: box-shadow 0.3s ease-in, opacity 0.3s ease-in;
			&:hover {
				opacity: 0.6;
				box-shadow: 0px 0px 10px 0px rgb(116, 116, 116);
			}
		}
	}
	&__user {
		color: $dark50;
		transition: text-shadow 0.15s ease-in;
		&:hover {
			text-shadow: 0px 0px 1px rgba(0, 0, 0, 1);
		}
		&-link {
			display: inline-block;
			border-left: 1px solid $dark10;
			padding: 7px 0 7px 20px;
			margin-left: 20px;
		}
	}
}
.active.nav__list {
	transform: translateX(0);
}
.active.header-btns {
	transform: translateX(0);
}
.nav {
	&__list {
		display: flex;
	}
	&__item {
		color: $dark50;
		line-height: 17px;
		&:hover {
			.nav__link--drop {
				path {
					fill-opacity: 1;
				}
			}
		}
		& + & {
			margin-left: 20px;
		}
	}
	&__link {
		display: inline-block;
		transition: color 0.15s ease-in, text-shadow 0.15s ease-in;
		&:hover {
			text-shadow: 0px 0px 1px rgba(0, 0, 0, 1);
			color: $dark;
		}
		&--drop {
			svg {
				margin-left: 7px;
				margin-right: 6px;
			}
			path {
				transition: fill-opacity 0.2s ease-in;
			}
		}
	}
}

@media (max-width: 941px) {
	.nav__list {
		width: 190px;
		flex-direction: column;
		position: fixed;
		right: 0;
		top: 0;
		background-color: #fff;
		padding: 20px;
		padding-top: 70px;
		z-index: 10;
		transform: translateX(100%);
		transition: transform 0.3s ease-in;
		border: 1px solid rgba(0, 0, 0, 0.12);
		box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
		border-radius: 0 0 0 4px;
	}
	.nav__item + .nav__item {
		margin-left: 0;
		margin-top: 20px;
	}
	.header-btns {
		margin-right: 65px;
		z-index: 10;
	}
	.burger {
		display: inline-block;
	}
	.submenu__list {
		left: 50%;
		transform: translateX(-50%);
	}
}
@media (max-width: 541px) {
	.nav__list {
		padding-top: 165px;
	}
	.header-btns {
		position: fixed;
		right: -57px;
		top: 70px;
		flex-direction: column;
		transform: translateX(130%);
		transition: transform 0.3s ease-in;
	}
	.header-btns__signup {
		margin-left: 0;
		margin-top: 10px;
		padding: 10px 10px;
	}
}
