.range-elements__title {
  margin-bottom: 5px;
}
.dropdown__input {
  width: 160px;
  border: none;
  font-size: 14px;
  color: $dark75;
  padding: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  pointer-events: none;
}
.dropdown {
  position: relative;
  margin-bottom: 40px;
  &__button {
    width: 266px;
    height: 44px;
    border: 1px solid $dark25;
    border-radius: 4px;
    color: $dark75;
    text-align: left;
    padding-left: 15px;
    position: relative;
    &:focus,
    &--active {
      outline: none;
      border-color: $dark50;
      // color: $dark;
    }
    &::after {
      position: absolute;
      content: "";
      width: 20px;
      height: 20px;
      right: 11px;
      top: 50%;
      transform: translateY(-50%);
      background-image: url("../img/arrow-down.svg");
      background-repeat: no-repeat;
      background-position: center;
      pointer-events: none;
    }
  }
  &__list {
    position: absolute;
    top: 43px;
    left: 0;
    background-color: #fff;
    border: 1px solid $dark50;
    box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
    border-radius: 0 0 4px 4px;
    width: 266px;
    padding: 5px 5px 9px 14px;
    opacity: 0;
    z-index: -1;
    transform: translateY(-50%);
    transition: transform 0.2s ease-in;
    &--active {
      z-index: 1;
      opacity: 1;
      transform: translateY(0);
    }
  }
  &__list-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 700;
    font-size: 12px;
    text-transform: uppercase;
    color: $dark;
    cursor: pointer;
    & + & {
      margin-top: 7px;
    }
  }
}
