.comment {
  max-width: 711px;
  color: $dark75;
  position: relative;
  &__author {
    font-size: 14px;
    font-weight: 700;
    margin-bottom: 7px;
    padding-left: 58px;
    position: relative;
    &::before {
      position: absolute;
      content: "";
      width: 58px;
      height: 58px;
      left: -5px;
      top: -9px;
      filter: drop-shadow(0px 10px 20px rgba(31, 32, 65, 0.1));
      background-image: url("../img/author.png");
      background-repeat: no-repeat;
      background-position: center;
    }
  }
  &__date {
    padding-left: 58px;
    margin-bottom: 16px;
  }
  &__text {
    line-height: 1.7;
    padding-left: 58px;
  }
}
.like-elements__like3 {
  position: absolute;
  left: 4px;
  top: 56px;
}
